.statusSideShow{
    display: -ms-flexbox;
    display: flex;
    padding: 1rem 1rem;
    cursor: pointer;
    border-radius: 4px;
    overflow: hidden;
}

.statusSideShow:active,.statusSideShow:hover{
    background-color: rgba(219, 209, 209, 0.6);
}

.statusSideShow img{
    width: 40px;
    height: 40px;
    border-radius: 100%;
}

.detials{
    -ms-flex-item-align: center;
        align-self: center;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    padding-left: 0.5rem;
}

.detials p{
    -ms-flex-item-align: start;
        align-self: flex-start;
}

.heading{
    font-size: 1.35rem;
    font-weight: 700;
}




