.searchbar{
    display: -ms-flexbox;
    display: flex;
    position: relative;
    width: 100%;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-line-pack: center;
        align-content: center;
}

.input{
    -ms-flex: 100% 0;
        flex: 100% 0;
    outline: none;
    border: none;
    border-radius: 40px;
    padding: 4px 10px;
    font-size: 1rem;
    color: rgb(110, 108, 108);
    caret-color: lightgreen;
}

.input:-ms-input-placeholder{
    color: #ccc;
}

.search{
    position: absolute;
    -ms-flex-item-align: center;
        align-self: center;
    right: 5px;
    font-weight: 700;
    font-style: normal;
    cursor: pointer;
}
.search::-moz-selection{
    cursor: none;
}
.search::selection{
    cursor: none;
}

.search:focus,.search:hover{
    color: lightgreen;
}


