.contactdetails{
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    background-color: #eee;
    padding: 14px 5px;
}

.contactdetails *:not(:first-child){
    -ms-flex-item-align: center;
        align-self: center;
}

.contactdetails img{
    width: 60px;
    height: 60px;
    border-radius: 100%;
}

.about{
    margin-right: auto;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    padding: 0 1rem;
}

.name{
    font-size: 1.4rem;
    font-weight: 700;
    -ms-flex-item-align: start!important;
        align-self: flex-start!important;
}

.lastseen{
    -ms-flex-item-align: start!important;
        align-self: flex-start!important;
}

.details{
    font-size: 1rem;
    cursor: pointer;
    padding: 0 1rem;
    color: green;
}

.details:hover{
    color: lightgreen;
}

.search{
    cursor: pointer;
    color: black;
}

.search:hover{
    color: lightgreen;
}


