.person{
    display: -ms-flexbox;
    display: flex;
    background-color: white;
    padding: 4px 8px;
    cursor: pointer;
    border-radius: 4px;
    text-decoration: none;
}

.active,
.person:hover{
    background-color: #ccc;
}


.person img{
    width: 60px;
    height: 60px;
    border-radius: 100%;
    -ms-flex-item-align: center;
        align-self: center;
}

.data{
    -ms-flex: 1 1;
        flex: 1 1;
    -ms-flex-item-align: center;
        align-self: center;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    padding-left: 1rem;
}

.name{
    -ms-flex-item-align: start;
        align-self: flex-start;
    justify-self: flex-end;
}

.msg{
    padding-left: 8px;
    font-size: 1.4rem;
    color: black;
    -ms-flex-item-align: center;
        align-self: center;
}

.time {
    -ms-flex-item-align: center;
        align-self: center;
    font-weight: 600;
}

.detailmsg{
    color: rgb(81, 195, 240);
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}


