 .userProfile{
     width: 100%;
     height: 100%;
     z-index: 200;
     background-color: white;
     left: 0;
     -webkit-animation: fade-in 0.5s linear 1;
             animation: fade-in 0.5s linear 1;
 }

 .userProfile img{
    width: 100%;
    height: 100%;
    border-radius: 100%;
}

 .showPhto{
     position: fixed;
     top: 50%;
     left:50%;
     -webkit-transform: translateX(-50%) translateY(-50%);
         -ms-transform: translateX(-50%) translateY(-50%);
             transform: translateX(-50%) translateY(-50%);
     z-index: 200;
     border-radius: 0px!important;
     width: 50%!important;
     height: 50%!important;

 }

 .photo{
    width: 100%;
    padding: 20px;
 }

 @-webkit-keyframes fade-in{
     0%{
        left: -100%;
        opacity: 0;
     }
     100%{
        left: 0;
        opacity: 1;
     }
 }

 @keyframes fade-in{
     0%{
        left: -100%;
        opacity: 0;
     }
     100%{
        left: 0;
        opacity: 1;
     }
 }


