.viewport{
    width: 100%;
    height: 100%;
    position: relative;
    padding: 16px 8px;
    z-index: 200;
    overflow: hidden;
}

.viewport > *{
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translateX(-50%) translateY(-50%);
        -ms-transform: translateX(-50%) translateY(-50%);
            transform: translateX(-50%) translateY(-50%);
    font-style: italic;
}

.viewport img{
    height: 80%;
}

.viewport p::-moz-selection{
    cursor: none;
}

.viewport p::selection{
    cursor: none;
}

.viewport span{
    top: 0;
    left: -100%;
    height: 4px;
    width: 100%;
    position: absolute;
    -webkit-transform: none;
        -ms-transform: none;
            transform: none;
    -webkit-animation: moveLeft 2s linear infinite;
            animation: moveLeft 2s linear infinite;
    background-color: #ccc;
}

@-webkit-keyframes moveLeft{
    0%{
        left: -102%;
    }
    100%{
        left: 0%
    }
}

@keyframes moveLeft{
    0%{
        left: -102%;
    }
    100%{
        left: 0%
    }
}

.viewport h2{
    position: absolute;
    top: 10px;
    left: 50%;
    -webkit-transform: translateX(-50%);
        -ms-transform: translateX(-50%);
            transform: translateX(-50%);
}


