.toolbar{
    position: relative;
    text-align: center;
    font-size: 30px;
    background-color: rgb(88, 170, 88);
    grid-row: 1/2;
    grid-column: 1/3;
    color: white;
    display: grid;
    -ms-flex-align: center;
        align-items: center;
}
.logo{
    position: absolute;
    top: 50%;
    left: 5%;
    -webkit-transform: translateY(-50%);
        -ms-transform: translateY(-50%);
            transform: translateY(-50%);
    width: 50px;
}


