.userDetials{
    padding: 10px 5px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-line-pack: center;
        align-content: center;
}

.image{
    width: 60px;
    height: 60px;
    border-radius: 100%;
}

.icons{
    -ms-flex-item-align: center;
        align-self: center;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-line-pack: center;
        align-content: center;
}

.icons div{
    margin: 4px;
    padding: 2px;
    color: lightgreen;
    cursor: pointer;
}

.icons div:hover{
    color: darkgreen;
}

.icons div::-moz-selection{
    cursor: none;
}

.icons div::selection{
    cursor: none;
}

.userDetials p{
    cursor: no-drop;
    -ms-flex-item-align: center;
        align-self: center;
    color: black;
    margin-right: auto;
    padding: 8px;
    font-size: 13px;
    text-transform: uppercase;
}


